import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { MdOutlineDeleteForever } from "react-icons/md";
import { FaPlus } from "react-icons/fa6";
import { AiOutlineClose } from "react-icons/ai";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { BiEdit } from "react-icons/bi";
import { useNavigate } from "react-router-dom";

import { PATH } from "../../../../utils/path";
import { ANSWER_TYPE } from "../../../../components/LookupData/Data";
import {
  adminQuestionStatus,
  adminRequiredStatus,
  createAdminPresForm,
  deleteAdminQuestion,
  getAdminPresForm,
  getAllAdminPresForm,
} from "../../../../app/features/OnlinePrescriptionForm/OnlinePrescriptionForm.slice";
import FullScreenLoader from "../../../../authorization/FullScreenLoader";

function Questionnaires() {
  const { handleSubmit, setValue, register } = useForm();
  const [selectedType, setSelectedType] = useState("401");
  const [sections, setSections] = useState([
    {
      questionId: "",
      question: "",
      options: [""],
      selectedType: "401",
      isActive: true,
      isRequired: true,
    },
  ]);
  const [editing, setEditing] = useState(false);

  const { adminOnlinePrescripionForm, isLoading, isError, isSuccess } =
    useSelector((state) => state?.onlinePrescriptionForm);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const searchParams = new URLSearchParams(window.location.search);
  let paramsData = searchParams.get("disease");

  const handleTypeChange = (e, index) => {
    setSelectedType(e.target.value);
    const updatedSections = [...sections];
    updatedSections[index].selectedType = e.target.value;
    setSections(updatedSections);
  };

  const handleAddSection = () => {
    setSections([
      ...sections,
      {
        questionId: "",
        question: "",
        options: [""],
        selectedType: "401",
        isActive: true,
        isRequired: true,
      },
    ]);
  };

  const handleQuestionChange = (sectionIndex, value) => {
    const updatedSections = [...sections];
    updatedSections[sectionIndex].question = value;
    setSections(updatedSections);
  };

  const handleAddOption = (sectionIndex) => {
    const updatedSections = [...sections];
    updatedSections[sectionIndex].options = [
      ...sections[sectionIndex].options,
      "",
    ];
    setSections(updatedSections);
  };

  // const handleOptionChange = (sectionIndex, optionIndex, value) => {
  //   const updatedSections = [...sections];
  //   updatedSections[sectionIndex].options[optionIndex] = value;
  //   setSections(updatedSections);
  // };

  const handleOptionChange = (sectionIndex, optionIndex, value) => {
    const updatedSections = [...sections];
    updatedSections[sectionIndex] = {
      ...updatedSections[sectionIndex],
      options: updatedSections[sectionIndex].options.map((option, index) =>
        index === optionIndex ? { ...option, answerLabel: value } : option
      ),
    };
    setSections(updatedSections);
  };

  // const handleCloseOption = (sectionIndex, optionIndex) => {
  //   const updatedSections = [...sections];
  //   updatedSections[sectionIndex].options.splice(optionIndex, 1);
  //   setSections(updatedSections);
  // };

  const handleCloseOption = (sectionIndex, optionIndex) => {
    const updatedSections = [...sections];
    updatedSections[sectionIndex] = {
      ...updatedSections[sectionIndex],
      options: updatedSections[sectionIndex].options.filter(
        (_, index) => index !== optionIndex
      ),
    };
    setSections(updatedSections);
  };

  const handleDeleteSection = (index) => {
    const updatedSections = [...sections];
    updatedSections.splice(index, 1);
    setSections(updatedSections);
  };

  // const handleSwitchChange = (sectionIndex, isChecked) => {
  //   console.log(`Switch in section ${sectionIndex} is now ${isChecked}`);
  // };

  const handleSwitchChange = (sectionIndex, isChecked) => {
    console.log(`Switch in section ${sectionIndex} is now ${isChecked}`);
    const updatedSections = [...sections];
    updatedSections[sectionIndex] = {
      ...updatedSections[sectionIndex],
      isActive: isChecked,
    };
    setSections(updatedSections);
  };

  const handleRequiredChange = (sectionIndex, isChecked) => {
    console.log(`Required in section ${sectionIndex} is now ${isChecked}`);
    const updatedSections = [...sections];
    updatedSections[sectionIndex] = {
      ...updatedSections[sectionIndex],
      isRequired: isChecked,
    };
    setSections(updatedSections);
  };

  function getPresForm() {
    const finalData = {
      formId: paramsData,
    };
    dispatch(getAdminPresForm(finalData));
  }

  function handleQuestionStatus(questionId) {
    let finalData = {
      questionId: questionId,
    };
    dispatch(adminQuestionStatus({ finalData, getPresForm }));
  }

  function handleRequiredStatus(questionId) {
    let finalData = {
      questionId: questionId,
    };
    dispatch(adminRequiredStatus({ finalData, getPresForm }));
  }

  useEffect(() => {
    const finalData = {
      formId: paramsData,
    };
    dispatch(getAdminPresForm(finalData));
  }, [dispatch, paramsData]);

  useEffect(() => {
    if (paramsData && adminOnlinePrescripionForm) {
      setSections([]);
      adminOnlinePrescripionForm?.data?.formQuestionList?.map(
        (section, sectionIndex) => {
          const newSection = {
            title: adminOnlinePrescripionForm?.data?.title,
            questionId: section.questionId,
            question: section.question,
            options: section.formAnswersList,
            selectedType: section.answerTypeId.toString(),
            isActive: section.isActive,
            isRequired: section.isRequired,
          };
          return setSections((prevSections) => [...prevSections, newSection]);
        }
      );
    }
  }, [paramsData, adminOnlinePrescripionForm]);

  function deleteQuestion(questionId) {
    let finalData = {
      questionId: questionId,
    };
    dispatch(deleteAdminQuestion({ finalData, getPresForm }));
  }

  function onCreateSuccess() {
    if (user?.roleId === 1) {
      navigate(PATH.SUPERADMIN_FORMS);
    } else if (user?.roleId === 4) {
      navigate(PATH.ADMIN_FORMS);
    }

    let finalData = {
      Search: "",
    };
    dispatch(getAllAdminPresForm(finalData));
  }

  function createOnlinePresForm(submittedData) {
    const finalData = {
      formId: paramsData ? adminOnlinePrescripionForm?.data?.formId : 0,
      title: submittedData?.title
        ? submittedData?.title
        : adminOnlinePrescripionForm?.data?.title,
      isActive: paramsData ? adminOnlinePrescripionForm?.data?.isActive : true,
      formQuestionList: sections.map((value, index) => ({
        questionId: value?.questionId ? value?.questionId : 0,
        question: value?.question,
        answerTypeId: value?.selectedType,
        questionOrder: index,
        isActive: value?.isActive,
        isRequired: value?.isRequired,
        answerTypeName:
          value?.selectedType === "401"
            ? "Radio"
            : value?.selectedType === "402"
            ? "Checkbox"
            : value?.selectedType === "404"
            ? "TextBox"
            : value?.selectedType === "405"
            ? "Single Check Box"
            : "",
        formId: paramsData ? adminOnlinePrescripionForm?.data?.formId : 0,
        formAnswersList: value.options.map((option, index) => ({
          formAnswereId: option?.formAnswereId ? option?.formAnswereId : 0,
          formQuestionId: option?.formQuestionId ? option?.formQuestionId : 0,
          answerLabel:
            value?.selectedType === "405" && !option?.answerLabel
              ? "Single Check Box Label"
              : option?.answerLabel
              ? option?.answerLabel
              : "Answer Text",
        })),
      })),
    };
    dispatch(createAdminPresForm({ finalData, onCreateSuccess }));
  }

  return (
    <>
      <h5>Questionnaires</h5>
      <Card className="shadow-sm custom-questionnaires">
        {isLoading ? (
          <FullScreenLoader />
        ) : isSuccess ? (
          <Form onSubmit={handleSubmit(createOnlinePresForm)}>
            <Row className="m-3 d-flex justify-content-center flex-column">
              <Col xl={7} lg={10} xs={12} className="mx-auto mb-3 p-0">
                <Form.Group
                  className="px-3 pt-2 pb-4 rounded mb-3"
                  controlId="formTitle"
                  style={{
                    boxShadow: "0px 0px 24px 0px #0000000A",
                    borderTop: "6px solid #000071",
                  }}
                >
                  <Form.Label
                    className="mt-2 fw-bold"
                    style={{ fontSize: "1.5rem" }}
                  >
                    {editing ? "Enter " : ""}Prescription Title
                  </Form.Label>
                  <Form.Control
                    type="text"
                    className="bg-transparent"
                    placeholder="Prescription Title"
                    maxLength={250}
                    // {...register("title", { required: true })}
                    name="title"
                    onChange={(e) => setValue("title", e.target.value)}
                    disabled={paramsData && !editing}
                    defaultValue={adminOnlinePrescripionForm?.data?.title}
                  />
                </Form.Group>
                {sections?.length === 0 ? (
                  <Button
                    variant="primary"
                    type="button"
                    className="Admin-Add-btn px-3 float-end"
                    onClick={handleAddSection}
                  >
                    <FaPlus size={24} />
                  </Button>
                ) : paramsData && !editing ? (
                  <Button
                    variant="primary"
                    type="button"
                    className="Admin-Add-btn px-3 float-end"
                    onClick={() => {
                      setEditing(true);
                    }}
                  >
                    <BiEdit size={24} /> Edit
                  </Button>
                ) : (
                  ""
                )}
              </Col>
              {sections.map((section, sectionIndex) => (
                <Col
                  key={sectionIndex}
                  xl={7}
                  lg={10}
                  xs={12}
                  className="p-3 rounded mx-auto mb-3"
                  style={{
                    boxShadow: "0px 0px 24px 0px #0000000A",
                    borderLeft: "6px solid #F26522",
                  }}
                >
                  <Form.Group
                    className="mb-3"
                    // controlId={`formQuestion${sectionIndex}`}
                  >
                    {(!paramsData || editing) && (
                      <div className="d-flex justify-content-between flex-wrap align-items-center custom-col-rev mb-2">
                        <Form.Label className="text-w-full mb-0">
                          Q.{`${sectionIndex + 1}.`} Enter Question
                        </Form.Label>
                        <Col xl={5} sm={6} xs={12}>
                          <Form.Select
                            aria-label="Select Type"
                            onChange={(e) => {
                              handleTypeChange(e, sectionIndex);
                            }}
                            defaultValue={section?.selectedType || selectedType}
                            disabled={paramsData && section?.questionId}
                            className="bg-transparent"
                          >
                            {ANSWER_TYPE.map((type) => {
                              return (
                                <option
                                  value={type?.lookupId}
                                  key={type?.lookupId}
                                >
                                  {type.name}
                                </option>
                              );
                            })}
                          </Form.Select>
                        </Col>
                      </div>
                    )}
                    <div
                      className={
                        section?.selectedType === "405"
                          ? "d-flex align-items-center justify-content-between"
                          : null
                      }
                    >
                      {section?.selectedType === "405" && (
                        <Form.Check
                          name="single-checkbox"
                          type="checkbox"
                          className="me-2"
                          disabled
                        />
                      )}
                      <Form.Control
                        type="text"
                        placeholder="Question"
                        maxLength={250}
                        defaultValue={section?.question}
                        // Set the value of the input
                        // value={section.question}

                        disabled={paramsData && !editing}
                        // {...register(`questionText${sectionIndex}`, {
                        //   required: true,
                        // })}
                        name={`questionText${sectionIndex}`}
                        required={true}
                        onChange={(e) =>
                          handleQuestionChange(sectionIndex, e.target.value)
                        }
                        className="bg-transparent"
                      />
                    </div>
                    <div className="mt-2 questionnaires-option">
                      {section?.selectedType === "401" && (
                        <>
                          {section.options.map((option, optionIndex) => (
                            <div
                              className="d-flex align-items-center"
                              key={`radio-${optionIndex}`}
                            >
                              <Form.Check
                                label={
                                  <>
                                    <Form.Control
                                      type="text"
                                      placeholder="Add option"
                                      className="border-0 ms-2 bg-transparent"
                                      defaultValue={option.answerLabel}
                                      disabled={paramsData && !editing}
                                      onChange={(e) =>
                                        handleOptionChange(
                                          sectionIndex,
                                          optionIndex,
                                          e.target.value
                                        )
                                      }
                                    />
                                  </>
                                }
                                name={`group${sectionIndex}`}
                                type="radio"
                                id={`radio-${optionIndex}`}
                                className="d-flex align-items-center mb-1 w-100"
                                disabled
                              />
                              {!paramsData && (
                                <span className="mx-3">
                                  <AiOutlineClose
                                    size={18}
                                    onClick={() =>
                                      handleCloseOption(
                                        sectionIndex,
                                        optionIndex
                                      )
                                    }
                                  />
                                </span>
                              )}
                            </div>
                          ))}
                          {(!paramsData || !section?.questionId) && (
                            <span
                              className="text-cursor-pointer text-decoration-underline"
                              style={{ color: "#3F8BFC", fontSize: "0.9em" }}
                              onClick={() => handleAddOption(sectionIndex)}
                            >
                              Add another option
                            </span>
                          )}
                        </>
                      )}
                      {section?.selectedType === "404" && (
                        <>
                          <Form.Control
                            as="textarea"
                            rows={3}
                            placeholder="Answer Text"
                            className="mt-2"
                            disabled={true}
                          />
                        </>
                      )}
                      {section?.selectedType === "402" && (
                        <>
                          {section.options.map((option, optionIndex) => (
                            <div
                              key={`checkbox-${optionIndex}`}
                              className="d-flex align-items-center"
                            >
                              <Form.Check
                                label={
                                  <>
                                    <Form.Control
                                      type="text"
                                      placeholder="Add option"
                                      className="border-0 ms-2 bg-transparent"
                                      defaultValue={option.answerLabel}
                                      disabled={paramsData && !editing}
                                      onChange={(e) =>
                                        handleOptionChange(
                                          sectionIndex,
                                          optionIndex,
                                          e.target.value
                                        )
                                      }
                                    />
                                  </>
                                }
                                name={`group${sectionIndex}`}
                                type="checkbox"
                                id={`checkbox-${optionIndex}`}
                                className="d-flex align-items-center mb-1 w-100"
                                disabled
                              />
                              {!paramsData && (
                                <span className="mx-3">
                                  <AiOutlineClose
                                    size={18}
                                    onClick={() =>
                                      handleCloseOption(
                                        sectionIndex,
                                        optionIndex
                                      )
                                    }
                                  />
                                </span>
                              )}
                            </div>
                          ))}
                          {(!paramsData || !section?.questionId) && (
                            <span
                              className="text-cursor-pointer text-decoration-underline"
                              style={{ color: "#3F8BFC", fontSize: "0.9em" }}
                              onClick={() => handleAddOption(sectionIndex)}
                            >
                              Add another option
                            </span>
                          )}
                        </>
                      )}
                    </div>
                  </Form.Group>
                  <div className="w-100 d-flex flex-wrap justify-content-end">
                    {(!paramsData || (paramsData && editing)) && (
                      <>
                        {sectionIndex === sections.length - 1 && (
                          <span style={{ borderRight: "1px solid #CACACA" }}>
                            <FaPlus
                              size={24}
                              color="#CACACA"
                              className="mx-2 text-cursor-pointer"
                              data-toggle="tooltip"
                              data-placement="top"
                              title="Add Question"
                              onClick={handleAddSection}
                            />
                          </span>
                        )}
                        <span
                          className="me-2"
                          style={{ borderRight: "1px solid #CACACA" }}
                        >
                          <MdOutlineDeleteForever
                            size={26}
                            color="#CACACA"
                            className="mx-2 text-cursor-pointer"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Delete"
                            // onClick={() => handleDeleteSection(sectionIndex)}
                            onClick={() => {
                              if (
                                paramsData &&
                                editing &&
                                section?.questionId
                              ) {
                                const questionId =
                                  section.options[sectionIndex]?.formQuestionId;
                                deleteQuestion(questionId);
                              } else {
                                handleDeleteSection(sectionIndex);
                              }
                            }}
                          />
                        </span>
                      </>
                    )}
                    <Form.Check
                      type="switch"
                      id={`custom-switch-${sectionIndex}`}
                      label="Active"
                      {...register("isActive")}
                      checked={section?.isActive === false ? false : true}
                      onChange={(e) => {
                        if (paramsData && editing && section?.questionId) {
                          const questionId = section.questionId;
                          handleQuestionStatus(questionId);
                        } else {
                          handleSwitchChange(sectionIndex, e.target.checked);
                        }
                      }}
                      disabled={paramsData && !editing}
                      className="me-2"
                    />
                    <Form.Check
                      type="switch"
                      id={`required-switch-${sectionIndex}`}
                      label="Required"
                      {...register("isRequired")}
                      checked={section?.isRequired === false ? false : true}
                      onChange={(e) => {
                        if (paramsData && editing && section?.questionId) {
                          const questionId = section.questionId;
                          handleRequiredStatus(questionId);
                        } else {
                          handleRequiredChange(sectionIndex, e.target.checked);
                        }
                      }}
                      disabled={paramsData && !editing}
                    />
                  </div>
                </Col>
              ))}
              {((sections?.length !== 0 && !paramsData) ||
                (paramsData && editing)) && (
                <Col>
                  <Button
                    variant="primary"
                    type="submit"
                    className="Admin-Add-btn fw-bold float-end"
                  >
                    Save
                  </Button>
                </Col>
              )}
            </Row>
          </Form>
        ) : isError ? (
          <span className="text-danger fst-italic">Network Error</span>
        ) : null}
      </Card>
    </>
  );
}

export default Questionnaires;
